import React, { useState, useEffect, useRef } from 'react';
import fp1 from './fp/1.png';
import fp2 from './fp/2.png';
import fp3 from './fp/3.png';
import fp4 from './fp/4.png';
import fp5 from './fp/5.png';
import fp6 from './fp/6.png';
import fp7 from './fp/7.png';
import fp8 from './fp/8.png';
import fp9 from './fp/9.png';
import fp10 from './fp/10.png';
import fp11 from './fp/11.png';
import fp12 from './fp/12.png';
// import logo from './logo.svg';
import './App.css';

function App() {
    const [hashs, setHashs] = useState([]);
    const [hashLength, setHashLength] = useState(256);
    const [compareHash, setCompareHash] = useState("")
    const [compareNewHash, setCompareNewHash]  = useState("");
    const [chekedPrints, setChekedPrints] = useState(0);
    const [processingNum, setProcessingNum] = useState(0);
    const [startTime, setStartTime] = useState(new Date(0));
    const [currentTime, setCurrentTime] = useState(new Date(0));
    const [time, setTime] = useState(new Date(0));
    const [imgFp, setImgFp ]= useState([fp1,fp2,fp3,fp4,fp5,fp6,fp7,fp8,fp9,fp10,fp11,fp12]);
    const [imgFpIndexFind, setImgFpIndexFind ]= useState(0);
    const [imgFpIndexTemp, setImgFpIndexTemp ]= useState(0);
    const [firstLoad, setFirstLoad] = useState(true);
    const [showInfo, setShowInfo] = useState(true);
    const [printFound, setPrintFound] = useState(false);
    let [processing, setProcessing] = useState(false);

    const requestRef = React.useRef()

    function randomString(len, charSet) {
        charSet = charSet || 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        let randomString = '';
        for (let i = 0; i < len; i++) {
            let randomPoz = Math.floor(Math.random() * charSet.length);
            randomString += charSet.substring(randomPoz,randomPoz+1);
        }
        return randomString;
    }

    function gen() {
        setCurrentTime(Date.now);
        // console.log(startTime+" - "+ currentTime)
        setTime(currentTime - startTime);
        let newHash = randomString(hashLength);
        // let lhashs = [];
        // if (sessionStorage.getItem("hashs")) {
        //     lhashs = JSON.parse(sessionStorage.getItem("hashs"));
        // }
        // let uniqHash = false;
        // let uniqHashAt = 0;
        // while (uniqHash) {
        //     uniqHashAt = uniqHashAt + 1;
        //     if (!lhashs.hasOwnProperty(newHash)) {
        //         uniqHash = true;
        //     } else {
        //         newHash = randomString(256);
        //         console.log("Dublicate hash, get new: "+newHash)
        //     }
        //     if (uniqHashAt > 1000) {
        //         uniqHash = true;
        //     }
        // }
        // console.log("new hash: "+newHash)
        setCompareNewHash(newHash);
        setImgFpIndexTemp(Math.floor(Math.random() * Math.floor(11)))
        if (compareHash === newHash) {
            setProcessing(false);
            setPrintFound(true)
            setImgFpIndexTemp(imgFpIndexFind)
        } else {
            setProcessingNum(processingNum + 1)
            // lhashs.push(newHash);
            // sessionStorage.setItem("hashs", JSON.stringify(lhashs));
            // setHashs(hashs);
        }
    }

    useEffect(() => {
        if (processing) {
            requestRef.current = requestAnimationFrame(gen);
            return () => cancelAnimationFrame(requestRef.current);
        }
    });
    // setHashs(hashs.push(randomString(256)))
    // setHashs(hashs.push(randomString(256)))
    // console.log(randomString(256));
    // alert(Math.random().toString(36).substring(2, 128))

    function getTimerString(milSec) {
        const days = Math.floor(milSec / 86400000);
        if (days === 0) {
            return new Date(time).toISOString().slice(11, -1);
        } else {
            return days+" days "+new Date(time).toISOString().slice(11, -1);
        }
    }

  return (
    <div className="App">
      <header className="App-header">
          {showInfo?
              <div className="infoText">
                  <span className="spanInfoText" onClick={() => {setShowInfo(!showInfo)}}> Hide information text</span>
                  <p><a href="https://youtu.be/IqEoZiX_G3c">This video</a> motivating me on creating this app.<br/>
                      In a nutshell, the question is: how unique are fingerprints?<br/>
                      In reality, it is difficult to verify...<br/>
                      But I think that purely hypothetically, programmatically, you can check this<br/>
                      Let's take for convention the fact that a fingerprint is a random set of characters of 256 characters (it seems to me this is close to reality)<br/>
                      Let's generate a random string of 256 characters, and try to generate something the same, and see how many iterations will pass (one iteration - one person) and how much time will be spent<br/>
                      <br/>
                      Press 'Start' button for start generate<br/>
                      Have fun ;)
                  </p>
              </div>:
              <div className="infoText">
                  <span className="spanInfoText" onClick={() => {setShowInfo(!showInfo)}}> Show information text</span>
              </div>}
          {!firstLoad?
              <div className="sumInfo">
              <p>{"Elapsed time: "+getTimerString(time)}</p>
              <p>{"Checked fingerprints: "+processingNum}</p>
          </div>:
              <div></div>}
          {!firstLoad?
              !printFound? <div className={"fingers"}>
              <div className="fingerP">
                  <p>Generated fingerprint</p>
                  <img src={imgFp[imgFpIndexFind]} />
                  {/*<p>{compareHash}</p>*/}
              </div>
              <div className="fingerP">
                  <p>Try gen same...</p>
                  <img src={imgFp[imgFpIndexTemp]} />
              </div>
                </div>
              :<div className="fingerP">
                  <p>Wow you did it! :)</p>
                  <img src={imgFp[imgFpIndexFind]} />
                  {/*<p>{compareHash}</p>*/}
              </div>:
              <div></div>}
          {/*<input value={compareHash}/>*/}
          {/*<input value={compareNewHash}/>*/}
          <button className="startBtn" onClick={() => {
              // if (!processing) {
              //     sessionStorage.setItem("hashs", [])
              // }
              if (processing) {
                  setProcessing(false);
              } else {
                  setStartTime(Date.now);
                  setFirstLoad(false);
                  setPrintFound(false)
                  setShowInfo(false)
                  setImgFpIndexFind(Math.floor(Math.random() * Math.floor(11)));
                  setCompareHash(randomString(hashLength));
                  setProcessingNum(0)
                  setProcessing(true);
              }
          }}>{processing ? "Stop" : "Start"}</button>
        {/*<img src={logo} className="App-logo" alt="logo" />*/}
        {/*<p>*/}
        {/*  Edit <code>src/App.js</code> and save to reload.*/}
        {/*</p>*/}
        {/*<a*/}
        {/*  className="App-link"*/}
        {/*  href="https://reactjs.org"*/}
        {/*  target="_blank"*/}
        {/*  rel="noopener noreferrer"*/}
        {/*>*/}
        {/*  Learn React*/}
        {/*</a>*/}
      </header>
    </div>
  );
}

export default App;
